
.swiper-slide {
background-position: center;
background-size: cover;
width: 200px;
height: 350px;
transition: .5s;
}

@media (max-width: 768px) {
.swiper-slide {
width: 250px;
height: auto;
}
}

.swiper-slide img {
display: block;
width: 100%;
height: 100%;
-o-object-fit: cover;
    object-fit: cover;
}

.my-container {
position: relative;
width: 100%;
display: block;
}

.my-container .swiper-my-container {
width: 70%;
position: absolute;
left: 50%;
bottom: -16vw;
-webkit-transform: translateX(-50%);
        transform: translateX(-50%);
z-index: 2;
padding-bottom: 3vw;
}

@media (max-width: 768px) {
.my-container .swiper-my-container {
bottom: -58vw;
padding-bottom: 8vw;
}
}

.title_wrapper {
display: -webkit-box;
display: -ms-flexbox;
display: flex;
-webkit-box-pack: center;
    -ms-flex-pack: center;
        justify-content: center;
-ms-flex-line-pack: center;
    align-content: center;
padding: 3vw 0 2vw 0;
}

@media (max-width: 768px) {
.title_wrapper {
padding-top: 13vw;
}
}

.title_wrapper .reactLogo {
width: 5vw;
height: 5vw;
margin-right: 1vw;
}

@media (max-width: 768px) {
.title_wrapper .reactLogo {
width: 25vw;
height: 25vw;
}
}

.title_wrapper .reactLogo img {
width: 100%;
height: 100%;
-o-object-fit: cover;
    object-fit: cover;
}

.title_wrapper .title_ {
color: #fff;
text-align: left;
font-size: 2vw;
}

@media (max-width: 768px) {
.title_wrapper .title_ {
font-size: 7vw;
}
}

.title_wrapper .title_ span {
display: block;
}

.swiper-pagination-fraction {
bottom: 0;
}

.swiper-pagination-bullet {
width: 25px;
height: 5px;
border-radius: 5px;
}

.swiper-button-next:after, .swiper-button-prev:after {
    color: white;
    font-weight: bold;

